@import "math";

$theme-primary-color: hsl(218,82%,58%);
$theme-secondary-color-1: hsl(39, 82%, 58%);
$theme-secondary-color-2: hsl(9, 82%, 58%);
$theme-name1-font-size: 60px;
$theme-name2-font-size: 30px;
$num-nav-items: 5;

$large-content-well-width: 900px;
$large-navbar-gap: 20px;
$large-menu-width: 200px;
$large-header-height: $large-menu-width;
$large-header-width: $large-content-well-width*0.6 + $large-navbar-gap + $large-menu-width;
$large-min-width: (($large-content-well-width/2 + $large-navbar-gap + $large-menu-width)*2);
$large-authoricon-fraction: 0.75;

$medium-min-gap: 50px;
$medium-min-width: $large-content-well-width + 2*$medium-min-gap;
$medium-header-height: 100px;
$medium-nav-menu-height: 60px;
$medium-authoricon-fraction: 0.75;

$small-header-height: $theme-name1-font-size*0.8;
$small-authoricon-fraction: 0.90;
$small-menuicon-fraction: 0.80;
$small-menu-width: 150px;

$posts-title-size: 40px;
$post-title-size: 30px;
$post-body-size: 20px;
$post-date-size: 10px;

// SMALL SCREEN DEVICES

* {
	font-family: Lato;
}

body {
	padding-top: $small-header-height;
	/*background-color: #fdfdfd;*/
	background-color: #e1e1e1;
}

code {
	font-family: Hack;
	span {
		font-family: Hack;
	}
}

figure.img-card {
	float: right;
	border-radius: 5px;
	padding: 5px;
	box-shadow: 0px 0px 10px black;
	margin: 15px;
	figcaption {
		text-align: center;
		font-size: 20px;
	}
}

figure.plain-image {
	float: right;
	padding: 5px;
	figcaption {
		text-align: center;
		font-size: 20px;
	}
}
a {
	//color: $theme-secondary-color-2;
	color: $theme-primary-color;
	text-decoration: none;
}
a:link {
	//color: $theme-secondary-color-2;
	color: $theme-primary-color;
}
a:unvisited {
	color: $theme-primary-color;
}
a:hover {
	//color: $theme-secondary-color-1;
	color: $theme-secondary-color-2;
}
a:active {
	//color: $theme-secondary-color-1;
	color: $theme-secondary-color-2;
}

.bibliography {
	a:link {
		text-decoration: none;
	}
	li:hover {
		background-color: #e1e1e1;
	}
}

.highlighter-rouge {
	background-color: #eaeaea;
	border-radius: 5px;
	border: 1px solid #c1c1c1;
	padding: 2px;
	max-height: 500px;
	overflow: auto;
	pre {
		margin: 10px;
	}
}

.link-button {
	position: relative;
	display: inline-block;
	background-color: #fdfdfd;
	font-size: 40px;
	/*padding: 10px;*/
	border-radius: 5px;
	margin: 5px;
}

.left-button {
	position: absolute;
	right: 50%;
}

.right-button {
	position: absolute;
	left: 50%;
}

.link-buttons {
	position: relative;
	margin-top: 10px;
	text-align: center;
	.no-link {
		color: #666;
	}
	a {
		/*display: inline-block;*/
		color: black;
	}
	a:link {
		color: black;
	}
	a:unvisited {
		color: black;
	}
	a:hover {
		color: $theme-primary-color;
	}
	a:active {
		color: $theme-primary-color;
	}
}

.navheader {
	position: fixed;
	width: 100%;
	height: $small-header-height;
	top: 0;
	left: 0;

	.navboxtheme {
		background-color: $theme-primary-color;
		color: white;
		box-shadow: 0px 2px 4px black;
	}

	.navbox1 {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;

		.navauthoricon {
			box-sizing: border-box;
			position: absolute;
			height: $small-header-height*$small-authoricon-fraction;
			width: $small-header-height*$small-authoricon-fraction;
			top: $small-header-height*(1.0-$small-authoricon-fraction)/2;
			left: $small-header-height*(1.0-$small-authoricon-fraction)/2;
			border-radius: 50%;
			border: 5px solid #333;
			transform: scaleX(-1);
		}

		.navnamebox {
			position: absolute;
			height: $small-header-height;
			font-size: $small-header-height;
			width: 100%;
			top: 0;
			left: 0;
			color: white;
			.navnamebox1 {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				white-space: nowrap;
				font-family: Lato, Arial;
				font-size: 70%;
			}

			.navnamebox2{
				display: none;
			}

		}

		.navmenubutton {
			position: absolute;
			height: $small-header-height;
			width: $small-header-height;
			top: 0;
			right: 0;
			border: 0;
			display: inline-block;
			input {
				position: absolute;
				display: none;
				height: $small-header-height;
				width: $small-header-height;
				top: 0;
				right: 0;
				border: 0;
				display: none;
			}
			.navmenubackground {
				position: absolute;
				height: $small-header-height;
				width: $small-header-height;
				top: 0;
				right: 0;
				background-color: $theme-primary-color;
			}
			.navmenuicon {
				color: white;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				font-size: $small-header-height*$small-menuicon-fraction;
			}

			input:checked ~ .navmenubackground {
				background-color: $theme-secondary-color-2;
				box-shadow: inset 0px 0px 4px black;
			}
			input:checked ~ .navbar {
				display: block;
			}

			.navmenubackground:hover {
				background-color: $theme-secondary-color-1;
				box-shadow: inset 0px 0px 4px black;
			}

			.navbar {
				position: fixed;
				top: $small-header-height;
				right: 0;
				width: $small-menu-width;
				display: none;
				z-index: 10;
				ul {
					box-sizing: border-box;
					box-shadow: 0px 2px 4px black;
					/*border: 1px solid black;*/
					display: inherit;
					width: 100%;
					margin: 0;
					padding: 0;
					z-index: inherit;
					li {
						box-sizing: border-box;
						display: inherit;
						width: 100%;
						/*border-bottom: 1px solid black;*/
						z-index: inherit;
						a {
							display: inherit;
							color: white;
							text-align: center;
							font-family: Lato;
							text-decoration: none;
							font-size: $theme-name2-font-size;
							background-color: $theme-primary-color;
							z-index: inherit;
						}
						.active {
							background-color: $theme-secondary-color-2;
						}
						a:link {
							color: white;
						}
						a:unvisited {
							color: white;
						}
						a:hover {
							color: white;
							background-color: $theme-secondary-color-1;
						}
						a:active {
							color: white;
						}
					}
					li:last-child {
						/*border-bottom: none;*/
					}
				}
			}
		}
	}
}

.content-well {
	overflow: hidden;
	margin: auto;
	padding-bottom: 20px;
	padding-left: 20px;
	padding-right: 20px;
	font-family: Lato;
	background-color: #fdfdfd;
	border-radius: 5px;
	h1 {
		font-size: 40px;
		text-align: center;
	}
	.date {
		margin-top: 5px;
		margin-bottom: 5px;
		text-align: center;
		font-size: 15px;
	}
	p {
		font-size: 20px;
		text-align: justify;
		line-height: 30px;
	}
}

.post-well {
	.post-well-title {
		font-family: Lato;
		font-size: $posts-title-size;
		text-align: center;
		font-weight: bold;
	}
	font-family: Lato;
	.post-date {
		font-size: 20px;
		text-align: center;
	}
	.post-title {
		font-size: 30px;
	}
	.post-container {
		position: relative;
		display: block;
		padding: 5px;
		margin-top: 10px;
		background-color: #fdfdfd;
		border-radius: 5px;
		a {
			text-decoration: none;
			color: black;
		}
		a:link {
			color: black;
		}
		a:unvisited {
			color: black;
		}
		a:hover {
			color: $theme-primary-color;
		}
		a:active {
			color: $theme-primary-color;
		}

		input[type='checkbox'] {
			margin: 0;
			position: absolute;
			top: 5px;
			right: 5px;
			display: inline-block;
			opacity: 0;
			width: 30px;
			height: 30px;
			z-index: 1;
		}
		input[type='checkbox'] ~ span {
			position: absolute;
			top: 5px;
			right: 5px;
			width: 30px;
			height: 30px;
			font-size: 30px;
		}
		input[type='checkbox'] ~ span.off {
			display: inline-block;
		}
		input[type='checkbox'] ~ .show {
			display: none;
		}
		input[type='checkbox'] ~ span.on {
			display: none;
		}
		input[type='checkbox']:checked ~ span.off {
			display: none;
		}
		input[type='checkbox']:checked ~ span.on {
			display: inline-block;
		}
		input[type='checkbox']:checked ~ .show {
			display: block;
		}
	}
}

