	}

	.post-well {
		width: $large-content-well-width;
		.post-well-title {
			font-family: Lato;
			font-size: $posts-title-size;
			text-align: center;
			font-weight: bold;
		}
		.post-container {
			position: relative;
			border-radius: 5px;
			/*box-shadow: 0px 0px 4px black;*/
			background-color: white;
			margin-top: 10px;
			width: $large-content-well-width;
			a.post-link {
				text-decoration: none;
				background-color: transparent;
				color: black;
	
				.post-content-container {
					background-color: #fdfdfd;
					border-radius: 5px;
					hr {
						color: #f1f1f1;
					}
					p {
						margin-top: 0;
					}
					.post-title {
						display: block;
						text-align: center;
						margin-left: auto;
						margin-right: auto;
						font-family: Lato;
						font-size: $post-title-size;
					}
					.post-date {
						display: block;
						text-align: center;
						margin-left: auto;
						margin-right: auto;
						font-family: Lato;
						font-size: $post-date-size;
					}
					p {
						font-family: Lato;
						font-size: $post-body-size;
						text-align: justify;
					}
				}
			}
			a.post-link:link {
				.post-content-container {
					background-color: #fdfdfd;
				}
			}
			a.post-link:unvisited {
				.post-content-container {
					background-color: #fdfdfd;
				}
			}
			a.post-link:hover {
				.post-content-container {
					background-color: #f1f1f1;
				}
			}
			a.post-link:active {
				.post-content-container {
					background-color: #f1f1f1;
				}
			}
		}
	}
}
